</template>
<script>
  import CardMenu from "./CardMenu.vue";
  import {mapActions} from 'vuex'
  export default {
    name: "todo",
    data() {
      return {
        isModalVisible: false,
        links: [
          {
            name: 'Assign task',
            action: () => {
              this.toggleMenu()
              alert('You cant assign tasks now')
            },
            type: 'default'
          },
          {
            name: 'Share',
            action: () => {
              this.toggleMenu()
             // this.$emit('toggleShareModal')
              //alert('You cant assign tasks now')
            },
            type: 'default'
          },
          {
            name: 'Mark as completed',
            action: () => {
              this.toggleMenu()
              alert('completed')
            },
            type: 'default'
          },
          {
            name: 'Archive',
            action: () => {
              this.archive(this.todo.card_id)
              this.toggleMenu()
              
             // alert('You cant assign tasks now')
            },
            type: 'default'
          },
          {
            name: 'Delete',
            action: () => {
              this.delete(this.todo.card_id)
              this.toggleMenu()
             // alert('You cant assign tasks now')
            },
            type: 'danger'
          }
        ]
      }
    },
    methods: {
      ...mapActions({
        delete: 'todos/ADD_TRASH',
        archive: 'todos/ADD_ARCHIVE'
      }),
      ClickAway() {
        this.isModalVisible = false;
      },
      details() {
        console.log(this.todo.card_id)
        this.$router.push({ name: 'Details', params: { id: this.todo.card_id } })
      },
      toggleMenu() {
        this.isModalVisible = !this.isModalVisible;
      },
      closeModal() {
        this.isModalVisible = false;
      },

    },
    components: {
      CardMenu
    },
    props: {
      todo: {
        type: Object,
      },
    },
  };
</script>
<style lang="scss" scoped>
  .todo_item {
    min-height: 10rem;
  }

  .taskcard {
    background: #ffffff;
    border: 1px solid #dadada;
    box-sizing: border-box;
    border-radius: 4px;
    max-width: 30em;
  }

  .task_title {
    color: #101010;
    font-size: 1.26rem;
    font-weight: bold;
  }

  .task_description {
    color: #959595;
    font-size: 13px;
  }

  .link_color {
    color: #1a61db;
    font-size: 1rem;
    font-weight: 700;
  }

  .line {
    background-color: #dadada;
    width: 100%;
    height: 1px;
    margin-top: 1rem;
    margin-bottom: 4px;
  }

  .collab {
    font-style: normal;
    font-weight: normal;
    font-size: 11px;
    line-height: 13px;
    letter-spacing: 0.1em;
    color: rgba(0, 0, 0, 0.6);
    margin-top: 8px;
    margin-bottom: 12px;
  }

  .gray_link {
    color: #7c7c7c;
    text-decoration-line: underline;
  }
  progress[value] {
  /* Reset the default appearance */
  -webkit-appearance: none;
   appearance: none;
  border-radius: .6em;
  height: 5px;
  //  box-shadow: 0 0 10px rgb(0 103 69 / 28%);
}
progress[value]::-webkit-progress-bar {
 
  border-radius: 2px;
  background-color: #E2ECF8;
}
progress[value]::-webkit-progress-value {
  background-color: #00B87C;
  border-radius: 2px;
}
</style>